import React, { Component } from 'react'
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Nurtion_TPN_cell.scss'
import { yyyymmddformat } from '../../Function/Timeformat'
import RemarkModal from '../CrossSectionView/RemarkModal'

class Nurtiontpncell extends Component {
    state = {
        EditMode: false,
        onEdit: false,
        remark: this.props.data.TPNRemarks
    }
    onMouseEnter() {
        this.setState(
            {
                EditMode: true
            }
        )
    }
    onMouseLeave() {
        this.setState(
            {
                EditMode: false
            }
        )
    }
    showModal = () => {
        this.setState({
            onEdit: true,
        });
    };

    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    Moduledisplay(time, title) {
        return (
            <RemarkModal caseinfo={this.props.caseinfo} type="nurtiontpn" createTime={time} name={title} visible={this.state.onEdit} remark={this.state.remark} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
        );
    }




    render() {
        function tpnstatusdata(datalist) {
            const datacell = datalist.map(function (info, index) {
                return (
                    <div key={index} style={{ display: "grid", gridTemplateRows: "1fr 1fr" }}>
                        <div style={{ border: "rgba(232,232,232,1) 1px solid", borderCollapse: "collapse" }}>
                            <centerstyle>
                                <t2>{info.item}</t2>
                            </centerstyle>
                        </div>
                        <div style={{ border: "rgba(232,232,232,1) 1px solid", borderCollapse: "collapse" }}>
                            <centerstyle>
                                <t2>{info.Data}</t2>
                            </centerstyle>
                        </div>
                    </div>
                );
            })
            return datacell
        }
        const { data } = this.props
        return (
            <div onMouseEnter={() => this.onMouseEnter()} onMouseLeave={() => this.onMouseLeave()} className='nurtiontpntablelayout'>
                <div className='nurtionTPNogdercell'>
                    <div className='cellfordisposename'>
                        <div className='tpncelltextdisplay'>
                            <t3>{data.TPNDispositionName}</t3>
                        </div>
                        <div className='cellforitem'>
                            {tpnstatusdata(data.TPNData)}
                        </div>
                    </div>
                    <div className='tpncelltextdisplay'>
                        <t3>{data.TPNStatus}</t3>
                    </div>
                    <div className='tpncelltextdisplay'>
                        <t3>{yyyymmddformat(data.TPNStartTime)}</t3>
                    </div>
                    <div className='tpncelltextdisplay'>
                        <t3>{yyyymmddformat(data.TPNEndTime)}</t3>
                    </div>
                    <div style={this.state.EditMode ? { display: "flex", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: "120px", background: "white", width: "77px", border: "rgba(238,238,238,1) 1px solid" } : { display: "none" }}>
                        <div  style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <div className='cellforNote'>
                    <t3>{this.state.remark}</t3>
                </div>
                {this.Moduledisplay(data.TPNStartTime, data.TPNDispositionName)}
            </div>
        );
    }
}

export default Nurtiontpncell