import React, { Component } from 'react';
import { Table, Input, Select, Button, Modal, Radio } from 'antd';
import * as ApiProvider from '../API/ApiProvider';

class Powersetting extends Component {
    state = {
        MouseClick: 0,
        name: undefined,
        role: undefined,
        userList: [],
        power: ['可管理所有角色帳號、可瀏覽和編輯平台筆記', '可管理一般管理者、一般使用者帳號及唯讀使用者帳號、可瀏覽和編輯平台筆記', '可瀏覽和編輯平台筆記', '僅可瀏覽平台'],
        visible: false
    }

    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };

    componentDidMount() {
        this.nameChange = this.nameChange.bind(this)
        this.queryUserdata = this.queryUserdata.bind(this)
    }

    MouseClickTable(record) {
        console.log(this.state.MouseClick)
        this.setState({
            MouseClick: record
        })
    }

    queryUserdata() {
        if (this.state.role !== undefined && this.state.name !== undefined) {
            ApiProvider.searchUser(this.state.name, this.state.role)
                .then(data => {
                    console.log(JSON.stringify(data))
                    this.setState({ userList: data.data })
                })
                .catch(err => { })
        }
    }

    EditStyle(key) {
        if (key === this.state.MouseClick) {
            return (
                <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gridColumnGap: '10px' }}>
                    <div>刪除</div>
                </div>
            );
        }
        else return null
    }

    handleChange(value) {
        this.setState({ role: value })
    }

    nameChange(event) {
        this.setState({ name: event.target.value })
    }

    Searchbar() {
        const { Option } = Select;
        const { Search } = Input;
        return (
            <div style={{ display: 'flex', alignItems: 'center' }}>
                <div style={{ display: 'flex', alignItems: 'center' }}>
                    <div style={{ fontSize: '16px' }}>
                        角色:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Select placeholder='選擇角色' style={{ width: 120 }} onChange={this.handleChange.bind(this)}>
                            <Option value={0}>主要管理者</Option>
                            <Option value={1}>一般管理者</Option>
                            <Option value={2}>唯獨使用者</Option>
                        </Select>
                    </div>
                </div>
                <div style={{ display: 'flex', alignItems: 'center', marginLeft: "18px" }}>
                    <div style={{ fontSize: '16px' }}>
                        姓名:
                    </div>
                    <div style={{ marginLeft: "10px" }}>
                        <Search placeholder="輸入個案姓名" onChange={this.nameChange} value={this.state.name} onSearch={this.queryUserdata}></Search>
                    </div>
                </div>
            </div>
        );
    }
    render() {

        const columns = [
            {
                title: '編號',
                dataIndex: 'id',
                key: 'id',
                width: "10%"
            },
            {
                title: '姓名',
                dataIndex: 'name',
                key: 'name',
                width: "10%"
            },
            {
                title: '角色',
                dataIndex: 'role',
                key: 'role',
                width: "10%"
            },
            {
                title: '權限',
                dataIndex: 'power',
                key: 'power',
                width: "48%"
            },
            {
                title: '帳號',
                dataIndex: 'account',
                key: 'account',
                width: "15%"
            },
            {
                dataIndex: 'clickstate',
                key: 'clickstate',
                width: "7%"
            }
        ];

        const userData = this.state.userList.map((data, index) => {
            let roleString = ''
            let powerString = ''
            if (data.role == 0) {
                roleString = '主要管理者'
                powerString = this.state.power[0]
            } else if (data.role == 1) {
                roleString = '一般管理者'
                powerString = this.state.power[1]
            } else if (data.role == 2) {
                roleString = '唯讀使用者'
                powerString = this.state.power[2]
            }
            return [
                {
                    key: ++index,
                    id: index++,
                    name: data.name,
                    role: roleString,  //0:主要管理者 1:一般管理者 2:唯獨使用者
                    power: powerString,
                    account: data.username,
                    clickstate: this.EditStyle(1)
                }
            ]
        })
        console.log(userData)

        return (
            <div>
                <div style={{ display: "flex", justifyContent: 'space-between', alignItems: 'center' }}>
                    {this.Searchbar()}
                    <div style={{ display: "flex", justifyContent: 'center', alignItems: "center" }}>
                        <Button style={{ width: "100%" }} onClick={this.showModal}>新增帳號</Button>
                        <Modal
                            title="新增帳號"
                            visible={this.state.visible}
                            onOk={this.handleOk}
                            onCancel={this.handleCancel}
                        >
                            <div>
                                <Radio>一般帳號</Radio>
                                <Radio>SSO</Radio>
                            </div>
                            <div>
                            <Input placeholder="Basic usage" />
                            </div>
                        </Modal>
                    </div>
                </div>
                <div style={{ marginTop: '20px' }}>
                    <Table columns={columns} dataSource={userData[0]} onRow={record => {
                        return {
                            onMouseEnter: event => { this.MouseClickTable(record.key) }, // 鼠标移入行
                            onMouseLeave: event => { this.MouseClickTable(0) } // 鼠标移入行
                        };
                    }} />
                </div>
            </div>
        );
    }
}

export default Powersetting;