import React, { Component } from 'react';
import './Nonlabviewtable.scss'
import { Nonlablist } from '../../Data/Wardviewdata-jack'
import { yyyymmddformat } from '../../Function/Timeformat'

class Nonlabviewtable extends Component {
    render() {
        function listforstatus(ordlist, ressect, pickerdate) {
            var status_report = []
            console.log(ordlist, ressect, pickerdate)
            const datadate = new Date(pickerdate).getTime()
            const dateselect = new Date(pickerdate).getTime() - (3 * 24 * 60 * 60 * 1000)
            if (Array.isArray(ordlist) && Array.isArray(ressect)) {
                for (let index = 0; index < ordlist.length; index++) {
                    const element = ordlist[index];
                    if (new Date(element.ORDATE).getTime() > dateselect && new Date(element.ORDATE).getTime() < datadate) {
                        const status = ressect.find(function (value, index, array) {
                            return value.ORDCLNM === element.ORDCLNM
                        })
                        if (status) {
                            status_report.push({
                                "status": true,
                                "element": element
                            })
                        }
                        else {
                            status_report.push({
                                "status": false,
                                "element": element
                            })

                        }
                    }
                }
            }
            return status_report
        }
        function statusflag(status) {
            switch (status) {
                case 0:
                    return '已開單'
                case 1:
                    return '已簽收'
                case 2:
                    return '已發報告'
                default:
                    return '--'
            }
        }
        const { PickerDate, ORDLIST, RESSECT } = this.props
        //Picker Date 前三天
        const list = listforstatus(ORDLIST, RESSECT, PickerDate)
        console.log(list)
        const nonlabcell = list.map(function (value, index, array) {
            const time = value.element.ORDATE
            const name = value.element.ORPROCED
            const status = value.status
            return (
                <div key={index} style={{ height: "50px", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
                    <t3 className='nonlabteblecell'>{time}</t3>
                    <t3 className='nonlabteblecell'>{name}</t3>
                    <t3 className='nonlabteblecell'>{status ? "已發報告" : "已開單"}</t3>
                    <t3 className='nonlabteblecell'>{"Result parse"}</t3>
                </div>
            );
        })

        return (
            <div>
                <div style={{ height: "50px", border: "#E2EDF3 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr" }}>
                    <t3 className='nonlabtebletitlecell'>檢驗日期</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目名稱</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗項目狀態</t3>
                    <t3 className='nonlabtebletitlecell'>檢驗結果</t3>
                </div>
                <div className='nonlabviewtable'>
                    {nonlabcell}
                </div>
            </div>
        );
    }
}

export default Nonlabviewtable