import React, { Component } from 'react';



class RTtable extends Component {
    render() {
        const GasTitleList = ['起迄', 'PH', 'PCO2', 'PO2', 'HCO3', 'BE', 'O2', 'Sample', 'FiO2']
        const GasTitleCell = GasTitleList.map(function (value, index, array) {
            return (
                <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                    <t3>{value}</t3>
                </centerstyle>
            );
        })
        const { PickerDate, RESDBGAS } = this.props
        function GasListToCell(Resdbgas, Pickerdate) {
            //for loop filter three days
            const GasCell = Resdbgas.map(function (value, index, array) {
                const ValueTime = value.ORBGNDT.toString() + " " + value.ORRESTM.toString()
                const PHValue = value.SS07
                const PCO2Value = value.SS06
                const PO2Value = value.SS08
                const HCO3Value = value.SS04
                const BEValue = value.SS01
                const SampleValue = "123" //Parse
                const O2SatValue = value.SS05
                const FiO2Value = value.SS03
                // console.log(value)
                const GasData = [ValueTime, PHValue, PCO2Value, PO2Value, HCO3Value, BEValue, SampleValue, O2SatValue, FiO2Value]
                const GasCell = GasData.map(function (value, index, array) {
                    return (
                        <centerstyle key={index} style={{ border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse" }}>
                            <t3>{value}</t3>
                        </centerstyle>
                    );
                })
                return (
                    <div key={index} style={{ height: "50px", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>{GasCell}</div>
                );
            })
            return GasCell
        }
        const GasCellData = GasListToCell(RESDBGAS, PickerDate)
        return (
            <div>
                <div style={{ height: "50px", border: "rgba(238,238,238,1) 1px solid", backgroundColor: "#E2EDF3", display: "grid", gridTemplateColumns: "110px repeat(8,1fr)" }}>
                    {GasTitleCell}
                </div>
                <div style={{ maxHeight: "400px", overflow: "auto" }}>
                    {GasCellData}
                </div>
            </div>
        );
    }
}

export default RTtable