import React, { Component } from 'react';
import Prescriptionordertable from './Prescriptionordertable'
import { DatePicker, Select, Input } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import './Prescriptionorder.scss'
import moment from 'moment'

var DateRange, SelectData
class Prescriptionorderlayout extends Component {
    state = {
        SelectMode: 0,
        InputMode: null,
        DateRangeMode: null
    }
    handleChange = (value) => {
        console.log(`selected ${value}`);
        SelectData = value
    }
    onDateRangeChange = (date, dateString) => {
        const st = date[0].valueOf()
        const et = date[1].valueOf()
        DateRange = {
            startTime:st,
            endTime:et
        }
    }
    onClickSearch() {
        const inputdata = document.getElementById("prescriptionorderinput").value
        if (inputdata === undefined || inputdata === " " || inputdata === null) {
            return null
        }
        if (DateRange === undefined || DateRange === null || DateRange === " ") {
            return null
        }
        if (SelectData === undefined || SelectData === null || SelectData === " ") {
            SelectData = this.state.selectMode
        }
        this.state({
            SelectMode:SelectData,
            InputMode:inputdata,
            DateRangeMode:DateRange
        })

    }
    ControlBarRender() {
        const { RangePicker } = DatePicker;
        const { Option } = Select;
        return (
            <div style={{ display: "grid", gridTemplateColumns: "220px 130px 1fr 25px", gridColumnGap: "10px", marginBottom: "5px" }}>
                <RangePicker placeholder={["請輸入日期", "請輸入日期"]} onChange={this.onDateRangeChange} allowClear={false} />
                <Select defaultValue={this.state.SelectMode} style={{ width: 120 }} onChange={this.handleChange}>
                    <Option value={0}>全</Option>
                    <Option value={1}>生效中</Option>
                    <Option value={2}>失效/已結束/取消</Option>
                </Select>
                <Input id="prescriptionorderinput" placeholder="輸入藥囑關鍵字" />
                <div onClick={() => this.onClickSearch()} style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <img src={searchlogo} alt="searchlogo" />
                </div>
            </div>
        );
    }
    render() {
        function SortDataList(){
            //Format UDORDER0
        }
        const { searchbar, PickerDate, UDORDER0 } = this.props
        const controlBar = this.ControlBarRender()
        return (
            <div>
                <div className={searchbar ? 'PrescriptionOrderSearchBar' : 'PrescriptionOrderNonSearchBar'}>
                    <t1>Prescription Order</t1>
                </div>
                {searchbar ? controlBar : null}
                <Prescriptionordertable UDORDER0={UDORDER0} PickerDate={searchbar ? PickerDate : null} ></Prescriptionordertable>
            </div>
        );
    }
}

export default Prescriptionorderlayout