import React, { Component } from 'react';
import Labview from './Labview'
import Nonlabview from './NonLabview'
import { DatePicker } from 'antd';
import searchlogo from '../../Image/svg/Search.svg'
import Examchart from './Examchart'
import Examaxis from './Examaxis'
import './ExamReport.scss'
import moment from 'moment'
import * as ApiProvider from '../../API/ApiProvider'
import Nonlabviewlayout from '../CrossSectionView/Nonlabview'

class Examreport extends Component {
    state = {
        Rangestate: null,
        selectdata: 0,
        PickerDate: moment(moment().format('YYYYMMDD')).valueOf(),
        RESDGLU1: [], //累積報告
        RESLAB02: [], //累積報告
        RESLAB01: [],  //累積報告
        RESDBGAS: [],  //累積報告
        RESLAB0C: [],//累積報告
        ORDLIST:[],
        RESSECT:[],
    }
    componentDidMount() {
        this.APIUpdate(this.state.PickerDate)
    }


    APIUpdate(Datetime) {
        function selectMonthInterval(PickerDate) {
            const timestampInterval = new Date().getTime() - new Date(PickerDate).getTime()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const { caseinfo } = this.props
        const st = moment(Datetime + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(Datetime + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const TRANSDate = moment(caseinfo.TRANSINDT, 'YYYYMMDD').format('YYYY-MM-DD')
        const MonthInterval = selectMonthInterval(Datetime)
        const RESDGLU1 = ApiProvider.getStoredProcedureRESDGLU1(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB02 = ApiProvider.getStoredProcedureRESLAB02(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB01 = ApiProvider.getStoredProcedureRESLAB01(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESDBGAS = ApiProvider.getStoredProcedureRESDBGAS(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB0C = ApiProvider.getStoredProcedureRESLAB0C(caseinfo.HISID, TRANSDate)
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });        
            const ORDLIST = ApiProvider.getStoredProcedureORDLIST(caseinfo.HISID, TRANSDate, "ALL", "DOC3924B")
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const RESSECT = ApiProvider.getStoredProcedureRESSECT(caseinfo.HISID, MonthInterval, "ALL", "DOC3924B")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        Promise.all([RESDGLU1, RESLAB02, RESLAB01, RESDBGAS, RESLAB0C,ORDLIST,RESSECT]).then(result => {
            this.setState({
                RESDGLU1: result[0], //累積報告
                RESLAB02: result[1],  //累積報告
                RESLAB01: result[2],  //累積報告
                RESDBGAS: result[3],  //累積報告
                RESLAB0C: result[4],  //累積報告
                ORDLIST:result[5],
                RESSECT:result[6],

            })
        })
    }
    callbackDateFunction = (value, select) => {
        console.log('Examreport')
        this.setState({
            Rangestate: value,
            selectdata: select
        })
    }

    charttableview() {
        if (this.state.Rangestate === null) {
            return null
        }
        const lab_item_list = [
            { item: "CBC檢驗:WBC、Hb、PLT" },
            { item: "電解質與腎功能檢驗:Na、K、Ca(Free,Total)、Mg、P、BUN、Cr" },
            { item: "肝功能檢驗:AST、ALT、rGGT、Alkp、Alb" },
            { item: "心臟功能檢驗:ck、ck-MB、BNP、Troponin I" },
            { item: "凝血功能檢驗:PT、APiT、INR" },
            { item: "GAS檢驗:PH、pCO2、pO2、HCO3、BE、O2、TCO2、SaCO2" },
            { item: "Glucose檢驗:糖化血色素、空腹血糖" },
        ]

        const row_title = [
            { item: 0, title: ["WBC", "Hb", "PLT"] },
            { item: 1, title: ["Na", "K", "Ca(Free,Total)", "Mg", "P", "BUN", "Cr"] },
            { item: 2, title: ["AST", "ALT", "rGGT", "Alkp", "Alb"] },
            { item: 3, title: ["ck", "ck-MB", "BNP", "Troponin I"] },
            { item: 4, title: ["PT", "APiT", "INR"], string: ["PT", "APiT", "INR"] },
            { item: 5, title: ["PH", "pCO2", "pO2", "HCO3", "BE", "O2", "TCO2", "SaCO2"] },
            { item: 6, title: ["糖化血色素", "空腹血糖"] }
        ]
        const chart = row_title[this.state.selectdata].title.map(
            (info, index) =>
                <div key={index} style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                    <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>{info}</div>
                    <div>
                        <Examchart Time={this.state.Rangestate} max={200} min={0} id_key={"id" + index} svg_id={"svg" + index}></Examchart>
                    </div>
                </div>
        )
        return (
            <div style={{ margin: "20px 0px" }}>
                <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                    <div></div>
                    <div>
                        <Examaxis axisTop={true} Time={this.state.Rangestate} axisBot={false} max={200} min={0} id_key={"id_axis_top_exam"} svg_id={"svg_axis_top_exam"}></Examaxis>
                    </div>
                </div>
                {chart}
                <div style={{ display: "grid", gridTemplateColumns: "140px 1fr" }}>
                    <div></div>
                    <div>
                        <Examaxis axisTop={false} Time={this.state.Rangestate} axisBot={true} max={200} min={0} id_key={"id_axis_bot_exam"} svg_id={"svg_axis_bot_exam"}></Examaxis>
                    </div>
                </div>
            </div>
        );
    }
    render() {
        const { caseinfo } = this.props
        const lab_item_list = [
            { item: "CBC檢驗:WBC、Hb、PLT" },
            { item: "電解質與腎功能檢驗:Na、K、Ca(Free,Total)、Mg、P、BUN、Cr" },
            { item: "肝功能檢驗:AST、ALT、rGGT、Alkp、Alb" },
            { item: "心臟功能檢驗:ck、ck-MB、BNP、Troponin I" },
            { item: "凝血功能檢驗:PT、APiT、INR" },
            { item: "GAS檢驗:PH、pCO2、pO2、HCO3、BE、O2、TCO2、SaCO2" },
            { item: "Glucose檢驗:糖化血色素、空腹血糖" },
        ]

        const row_title = [
            { item: 0, title: ['WBC', 'Band', 'Seg', 'Lym', 'Hb', 'Hct', 'PLT'] },
            { item: 1, title: ["Na", "K", 'Cl', "Ca(Free)", "Ca(Total)", "Mg", "P", "BUN", "Cr"] },
            { item: 2, title: ["AST", "ALT", "rGGT", "Alkp", "Alb"] },
            { item: 3, title: ["ck", "ck-MB", "BNP", "Troponin I"] },
            { item: 4, title: ["PT", "APiT", "INR"], string: ["PT", "APiT", "INR"] },
            { item: 5, title: ["PH", "pCO2", "pO2", "HCO3", "BE", "O2", "TCO2", "SaCO2"] },
            { item: 6, title: ["糖化血色素", "空腹血糖"] }
        ]
        const data = this.state.RESLAB0C
        if (data.length === 0) {
            return null
        }
        return (
            <div className='examreportlayout'>
                <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" }}>
                    <Labview selectvalue={this.state.selectdata} parentDateCallback={this.callbackDateFunction} lab_item_list={lab_item_list} row_title={row_title}></Labview>
                    <Nonlabviewlayout ORDLIST={this.state.ORDLIST} RESSECT={this.state.RESSECT} caseinfo={caseinfo} PickerDate={this.state.PickerDate}></Nonlabviewlayout>
                </div>
                {/* {this.charttableview()} */}
                <div style={{ display: "flex", justifyContent: 'center', alignItems: 'center' }}>
                    {/* <img src={demo} alt='demo' width='1000' height='400'/> */}
                </div>
            </div>
        );
    }
}

export default Examreport;