import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './Treatmentordertablecell.scss'
import RemarkModal from '../CrossSectionView/RemarkModal'

class Treatmentordertablecell extends Component {
    state = {
        onEdit: false,
        onMove: false,
        remark: this.props.info.remark
    }
    on_Mouse() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: true
                }
            )
        }
    }
    on_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onMove: false
                }
            )
        }
    }
    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    render() {
        const { info } = this.props
        const Name = info.ORPROCED
        const QD = info.ORFREQN
        const Status = info.ORSTATUS
        const ST = info.ORBGNDT.toString()
        const ET = info.ORENDDT.toString()
        // console.log(info)
        return (
            <div onMouseMove={() => this.on_Mouse()} onMouseLeave={() => this.on_leave()} className="treatmentordercell">
                <div style={{ display: "grid", gridTemplateRows: "80px 50px" }}>
                    <div style={{ display: "grid", gridTemplateColumns: "3fr 1fr 1fr 1fr 1fr" }}>
                        <t3 className='cellstyle'>{Name}</t3>
                        <t3 className='cellstyle'>{QD}</t3>
                        <t3 className='cellstyle'>{Status}</t3>
                        <t3 className='cellstyle'>{ST}</t3>
                        <t3 className='cellstyle'>{ET}</t3>
                    </div>
                    <t3 className='cellstyle'>{this.state.remark}</t3>
                    <div style={this.state.onMove ? { display: "flex", justifyContent: "center", alignItems: "center", position: 'absolute', right: 0, height: "130px",background:"white",width:"77px",border:"rgba(238,238,238,1) 1px solid" } : { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
                <RemarkModal caseinfo={this.props.caseinfo} type="treatment" createTime={ST} name={Name} visible={this.state.onEdit} remark={this.state.remark} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
            </div>
        );
    }
}
export default Treatmentordertablecell