import React, { Component } from 'react';
import { DatePicker, Button, Select } from 'antd';
import filterlogo from '../../Image/svg/filter.svg'
import Inoutonchronic from './Chronic_io'
import Labonchronic from './Chronic_lab'
import Rtonchronic from './Chronic_rt'
import Orderonchronic from './Chronic_order'
import Weightonchronic from './Chronic_weight'
import Vitalonchronic from './Chronic_vital'
import Vitalxaxis from '../../Module/Vitalxaxis'
import Vitalxaxisbot from '../../Module/Vitalxaxisbot'
import moment from 'moment'
import './Chroniclelayout.scss'
import X2JS from 'x2js';
import { yyyymmddHHmmfromat, HISAPI_stringtodate, yyyymmddformat } from '../../Function/Timeformat'
import * as ApiProvider from '../../API/ApiProvider'

class Chroniclelayout extends Component {
    state = {
        display_Mode: false,
        item: this.props.item_array,
        scrollstate: 0,
        PickerDate: moment(moment().format('YYYYMMDD')),
        select_interval: 0,
        RESSECT: [], //IO要用的
        QSPE: [], //累積報告
        UDORDER0: [],  //累積報告
        QTWPER: [], //累積報告
        QDC: []
    }
    componentDidMount() {
        this.APIDataFetch(this.state.PickerDate)
    }

    APIDataFetch(Datetime) {
        function selectMonthInterval(PickerDate) {
            const timestampInterval = new Date().getTime() - new Date(PickerDate).getTime()
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const { caseinfo } = this.props
        const xm2 = new X2JS()
        const st = moment(Datetime + (7 * 60 * 60 * 1000)).format('YYYYMMDDHHmm')
        const et = moment(Datetime + (31 * 60 * 60 * 1000)).format('YYYYMMDDHHmm')
        const TRANSDate = moment(caseinfo.TRANSINDT, 'YYYYMMDD').format('YYYY-MM-DD')
        const MonthInterval = selectMonthInterval(Datetime)
        const QDC = ApiProvider.getQDC(caseinfo.CASEID, st, et)
            .then(data => {
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                return parsedata

            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESSECT = ApiProvider.getStoredProcedureRESSECT(caseinfo.HISID, MonthInterval, "ALL", "DOC3924B")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const QSPE = ApiProvider.getQSPE(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data).outmsg.resconts.rescont)
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                if (Array.isArray(parsedata)) {
                    return parsedata
                }
                else {
                    const arr = []
                    arr.push(parsedata)
                    console.log(arr)
                    return arr
                }
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const UDORDER0 = ApiProvider.getStoredProcedureUDORDER0(caseinfo.HISID, caseinfo.CASEID, "123")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const QTWPER = ApiProvider.getQTWPER(caseinfo.CASEID, st, et)
            .then(data => {
                var weightlist = []
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                if (Array.isArray(parsedata)) {
                    for (let index = 0; index < parsedata.length; index++) {
                        const element = parsedata[index];
                        if (element.type === 'TW') {
                            weightlist.push({
                                "value": Number(element.value2) * 1000,
                                "date": element.rec_dat
                            })
                        }
                    }
                }
                else {
                    if (parsedata.type === 'TW') {
                        weightlist.push({
                            "value": Number(parsedata.value2) * 1000,
                            "date": parsedata.rec_dat
                        })
                    }
                }
                return weightlist
            })
            .catch(err => {
                console.log("Andy test getQTWPER NG " + JSON.stringify(err));
            });
        Promise.all([RESSECT, QSPE, UDORDER0, QTWPER, QDC]).then(result => {
            console.log("API DATA = " + result[8])
            this.setState({
                RESSECT: result[0], //IO要用的
                QSPE: result[1], //累積報告
                UDORDER0: result[2],  //累積報告
                QTWPER: result[3],  //累積報告
                QDC: result[4]
            })
        })
    }

    scrollToAnchor = (anchorName) => {
        console.log(anchorName)
        if (anchorName) {
            let anchorElement = document.getElementById(anchorName);
            if (anchorElement) { anchorElement.scrollIntoView(false); }
        }
    }
    scroll_btn_style(index) {
        console.log(index, this.state.scrollstate)
        if (index === this.state.scrollstate) {
            return true
        }
        else return false
    }
    onChange = (date, dateString) => {
        console.log(date, dateString);

        this.setState(
            {
                PickerDate: date.valueOf()
            }
        )
    }
    onSelectchange = (value) => {
        this.setState(
            {
                select_interval: value
            }
        )
    }
    render() {
        const { Option } = Select;
        const dateFormat = 'YYYY/MM/DD';
        const { caseinfo } = this.props
        const crosssectioncontrollist =
            [{ "name": "生命徵象", "scrollpoint": "vital_sign_chronic" },
            { "name": "輸出入", "scrollpoint": "input_output_chronic" },
            { "name": "檢驗結果", "scrollpoint": "lab_chronic" },
            { "name": "呼吸評估", "scrollpoint": "rt_chronic" },
            { "name": "醫藥囑", "scrollpoint": "order_chronic" },
            { "name": "體重", "scrollpoint": "infection_chronic" }]

        let button = crosssectioncontrollist.map(
            (item, index) => <Button onClick={this.scrollToAnchor.bind(this, item.scrollpoint)} key={index} style={{ borderRadius: "16px", color: "rgba(245, 166, 35, 1)", border: "rgba(245, 166, 35, 1) 1px solid" }}>{item.name}</Button>);
        const status = this.state.RESSECT
        if (status.length === 0) {
            return null
        }
        return (
            <div style={{ paddingLeft: "20px", paddingRight: "20px" }}>
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", height: "40px" }}>
                    <div>
                        <DatePicker onChange={this.onChange} defaultValue={moment(yyyymmddformat(this.state.PickerDate), dateFormat)} allowClear={false} />
                        <Select defaultValue={this.state.select_interval} style={{ width: 120, marginLeft: "20px" }} onChange={this.onSelectchange}>
                            <Option value={0}>一天</Option>
                            <Option value={1}>三天</Option>
                            <Option value={2}>一週</Option>
                            <Option value={3}>兩週</Option>
                        </Select>
                    </div>
                    <div style={{ display: "grid", gridTemplateColumns: "repeat(8,auto)", gridColumnGap: "15px" }}>
                        <t2 style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>項目切換:</t2>
                        {button}
                        <div style={{ display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={filterlogo} alt='filterlogo'></img>
                        </div>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px", margin: "10px 0px" }}>
                    <div></div>
                    <Vitalxaxis max={200} min={0} select_interval={this.state.select_interval} select_date={this.state.PickerDate} id_key="xaxis_chronic" svg_id="xaxis_svg_chronic"></Vitalxaxis>
                </div>
                <div className='boxshadow'></div>
                <div className='chroniclayout'>
                    {/* Vital sign */}
                    <div id="vital_sign_chronic">
                        <Vitalonchronic defaultdate={this.state.PickerDate} select_date={this.state.PickerDate} select_interval={this.state.select_interval} caseinfo={caseinfo} title="Vital"></Vitalonchronic>
                    </div>
                    {/* I/O */}
                    <div id="input_output_chronic">
                        <Inoutonchronic PickerDate={this.state.PickerDate} QDCLIST={this.state.QDC} IntervalStatus={this.state.select_interval} caseinfo={caseinfo} title="輸出入"></Inoutonchronic>
                    </div>
                    {/* Lab */}
                    <div id="lab_chronic">
                        <Labonchronic PickerDate={this.state.PickerDate} RESSECT={this.state.RESSECT} select_interval={this.state.select_interval} caseinfo={caseinfo} title="檢驗結果"></Labonchronic>
                    </div>
                    {/* RT */}
                    <div id="rt_chronic">
                        <Rtonchronic select_interval={this.state.select_interval} caseinfo={caseinfo} title="I/O"></Rtonchronic>
                    </div>
                    {/* Order */}
                    <div id="order_chronic">
                        <Orderonchronic PickerDate={this.state.PickerDate} UDORDER0={this.state.UDORDER0} select_interval={this.state.select_interval} caseinfo={caseinfo} title=""></Orderonchronic>
                    </div>
                    {/* 體重 */}
                    <div id="infection_chronic">
                        <Weightonchronic select_interval={this.state.select_interval} caseinfo={caseinfo} title=""></Weightonchronic>
                    </div>
                </div>
                <div style={{ display: "grid", gridTemplateColumns: "90px 1fr", height: "10px", marginTop: "30px" }}>
                    <div></div>
                    <Vitalxaxisbot max={200} min={0} select_interval={this.state.select_interval} select_date={this.state.PickerDate} id_key="xaxis_bot_chronic" svg_id="xaxis_bot_svg_chronic"></Vitalxaxisbot>
                </div>
            </div>
        );
    }
}

export default Chroniclelayout;