import React, { Component } from 'react';
import WardChart from './Wardchart'
import Wardinfousercheckbox from './Wardunfousercheckbox'
import Wardlabview from './Wardlab'
import { Modal, Select } from 'antd';
import Wardgrewchart from './Wardgrewchart'
import { bednumberformat, gender, pregancyweeksanddays } from '../Function/Wardbedfunction'
import fileLogo from '../Image/svg/file1.svg'
import file2Logo from '../Image/svg/file2.svg'
import './Wardinfouser.scss'
import { wardstatestringformat } from '../Function/Wardstate'
import moment from 'moment';
import * as ApiProvider from '../API/ApiProvider';
import X2JS from 'x2js';
import Labviewlayout from '../Patientview/CrossSectionView/Labview'

class Wardinfouser extends Component {
    state = {
        alertstate: "none",
        Data_change: true,
        visible: false,
        source: false,

        PBASINFO: [],
        Bornweek: 30,
        Bornday: 2,
        RESSECT: [],
        ORDLIST: [],
        TRANSINID: " ",
        TRANSINDT: "20191212",
        TRANSINTM: "1234",
        PDOCNEW: [],
        BornWeight: 0,
        WeightList: [],
        RESLAB01: [],
        RESSECTONEDAY: [],
        RESLAB01: [],
        RESDGLU1: [],
        RESDBGAS: [],
        RESLAB0C: []
    }
    sendData = (idnumber) => {
        this.props.parentCallback(idnumber);
    }

    componentDidMount() {
        const xm2 = new X2JS()
        //data = ALL HBED
        const { data } = this.props
        console.log(data)
        const source = data
        const caseid = source.PCASENO
        const hisid = source.PHISTNUM
        const PBASINFO = ApiProvider.getTablePBASINFO(hisid)
            .then(data => {
                this.setState({
                    PBASINFO: data.data
                })
                const PBASINGNINFO = data.data
                const starttime = moment(PBASINGNINFO.PBIRTHDT, 'YYYYMMDD').format('YYYYMMDDHHmm')
                const endtime = moment().format('YYYYMMDDHHmm')
                ApiProvider.getQTWPER(caseid, starttime, endtime)
                    .then(data => {
                        const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                        var weightlist = []
                        var birthdayweight = 0
                        for (let index = 0; index < parsedata.length; index++) {
                            const element = parsedata[index];
                            if (element.type === 'TW') {
                                if (element.rec_dat === PBASINGNINFO.PBIRTHDT) {
                                    birthdayweight = Number(element.value2) * 1000
                                }
                                weightlist.push(Number(element.value2) * 1000)
                            }
                        }
                        this.setState({
                            BornWeight: birthdayweight,
                            WeightList: weightlist
                        })
                        // return {
                        //     BornWeight: birthdayweight,
                        //     WeightList: weightlist
                        // }

                    })
                    .catch(err => {
                        console.log("Andy test getQTWPER NG " + JSON.stringify(err));
                    });
            })
            .catch(err => {
                console.log("Andy test getTableHBED NG " + JSON.stringify(err));
            });

        const QMNC = ApiProvider.getQMNC(caseid)
            .then(data => {
                console.log(xm2.xml2js(data.data))
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                // this.setState({
                //     bornweek: parsedata.weeks,
                //     bornday: parsedata.days,
                // })
                return {
                    Bornweek: parsedata.weeks,
                    Bornday: parsedata.days,
                }
            })
            .catch(err => {
                console.log("Andy test getQMNC NG " + JSON.stringify(err));
            });

        const RESSECT = ApiProvider.getStoredProcedureRESSECT(hisid, 1, "ALL", "DOC3924B")
            .then(data => {
                const ReportList = data.data
                const nowTime = new Date().getTime() - (24 * 60 * 60 * 1000)
                const mapList = ReportList.filter(function (value, index, array) {
                    const DTstring = value.ORRCPDT.toString()
                    const TMstring = value.ORRCPTM.toString()
                    const time = DTstring + TMstring
                    return moment(time, 'YYYY-MM-DDHH:mm:ss').valueOf() > nowTime
                })
                // this.setState({
                //     report: mapList.length
                // })
                return {
                    RESSECTONEDAY: mapList,
                    RESSECT: data.data
                }
            })
            .catch(err => {
                console.log("Andy test getTableHBED NG " + JSON.stringify(err));
            });
        const daybeforoneday = new Date().getTime() - (24 * 60 * 60 * 1000)
        const ORDLIST = ApiProvider.getStoredProcedureORDLIST(hisid, moment(daybeforoneday).format('YYYY-MM-DD'), "ALL", "DOC3924B")
            .then(data => {
                function datasize(datalist) {
                    const list = []
                    for (let index = 0; index < datalist.length; index++) {
                        const element = datalist[index];
                        if (element.ORSTATUS === "31") {
                            list.push(element)
                        }
                    }
                    return list.length
                }
                return datasize(data.data)
            })
            .catch(err => {
                console.log("Andy test getTableHBED NG " + JSON.stringify(err));
            });
        const PLOC = ApiProvider.getTablePLOC(caseid)
            .then(data => {
                const matadata = data.data
                const i = matadata.length
                const d = matadata[i - 1]
                const transintime = d.PLOCTM.toString()
                const transindt = d.PLOCDT.toString()
                const transinid = "NICU" + transindt + transintime
                return {
                    TRANSINID: transinid,
                    TRANSINDT: transindt,
                    TRANSINTM: transintime
                }
            })
            .catch(err => {
                console.log("Andy test getTableHBED NG " + JSON.stringify(err));
            });
        const PDOCNEW = ApiProvider.getTablePDOCNEW(caseid)
            .then(data => {
                const MataData = data.data
                var doc_list = []
                for (let index = 0; index < MataData.length; index++) {
                    const element = MataData[index];
                    doc_list.push({
                        PDTYPE: element.PDTYPE,
                        PDMDNO: element.PDMDNO,
                        PDDOCNMC: element.PDDOCNMC
                    })
                }
                return doc_list
            })
            .catch(err => {
                console.log("Andy test getTablePDOCNEW NG " + JSON.stringify(err));
            });

        Promise.all([PBASINFO, QMNC, RESSECT, ORDLIST, PLOC, PDOCNEW])
            .then(result => {
                console.log("Data =" + result)
                const TRANSDate = moment(result[4].TRANSINDT, 'YYYYMMDD').format("YYYY-MM-DD")
                const RESLAB01 = ApiProvider.getStoredProcedureRESLAB01(hisid, TRANSDate)
                    .then(data => {
                        return data.data
                    })
                    .catch(err => {
                        console.log("Andy test getQDC NG " + JSON.stringify(err));
                    });
                const RESDGLU1 = ApiProvider.getStoredProcedureRESDGLU1(hisid, TRANSDate)
                    .then(data => {
                        return data.data
                    })
                    .catch(err => {
                        console.log("Andy test getQDC NG " + JSON.stringify(err));
                    });
                const RESLAB02 = ApiProvider.getStoredProcedureRESLAB02(hisid, TRANSDate)
                    .then(data => {
                        return data.data
                    })
                    .catch(err => {
                        console.log("Andy test getQDC NG " + JSON.stringify(err));
                    });
                const RESDBGAS = ApiProvider.getStoredProcedureRESDBGAS(hisid, TRANSDate)
                    .then(data => {
                        return data.data
                    })
                    .catch(err => {
                        console.log("Andy test getQDC NG " + JSON.stringify(err));
                    });
                const RESLAB0C = ApiProvider.getStoredProcedureRESLAB0C(hisid, TRANSDate)
                    .then(data => {
                        console.log(data.data)
                        return data.data
                    })
                    .catch(err => {
                        console.log("Andy test getQDC NG " + JSON.stringify(err));
                    });
                return {
                    Bornweek: result[1].Bornweek,
                    Bornday: result[1].Bornday,
                    RESSECTONEDAY: result[2].RESSECTONEDAY,
                    RESSECT: result[2].RESSECT,
                    ORDLIST: result[3],
                    TRANSINID: result[4].TRANSINID,
                    TRANSINDT: result[4].TRANSINDT,
                    TRANSINTM: result[4].TRANSINTM,
                    PDOCNEW: result[5],
                    RESLAB01: RESLAB01,
                    RESDGLU1: RESDGLU1,
                    RESDBGAS: RESDBGAS,
                    RESLAB0C: RESLAB0C,
                    RESLAB02: RESLAB02
                }
            })
            .then(data => {
                this.setState({
                    Bornweek: data.Bornweek,
                    Bornday: data.Bornday,
                    RESSECTONEDAY: data.RESSECTONEDAY,
                    RESSECT: data.RESSECT,
                    ORDLIST: data.ORDLIST,
                    TRANSINID: data.TRANSINID,
                    TRANSINDT: data.TRANSINDT,
                    TRANSINTM: data.TRANSINTM,
                    PDOCNEW: data.PDOCNEW,
                    RESLAB01: data.RESLAB01,
                    RESDGLU1: data.RESDGLU1,
                    RESDBGAS: data.RESDBGAS,
                    RESLAB0C: data.RESLAB0C,
                    RESLAB02: data.RESLAB02
                })
            })
            .catch(err => {
                console.log("Error")
            })
    }



    showModal = () => {
        this.setState({
            visible: true,
        });
    };

    handleOk = e => {
        this.setState({
            visible: false,
        });
    };

    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };


    Doc_Display() {
        const PDOCNEW = this.state.PDOCNEW
        const Bdoc = PDOCNEW.filter(function (value, index, arr) {
            return value.PDTYPE === "B"
        })
        const Udoc = PDOCNEW.filter(function (value, index, arr) {
            return value.PDTYPE === "U"
        })
        const Ddoc = PDOCNEW.filter(function (value, index, arr) {
            return value.PDTYPE === "D"
        })
        const Ndoc = PDOCNEW.filter(function (value, index, arr) {
            return value.PDTYPE === "N"
        })


        return (
            <Modal
                width={480}
                title="醫療資訊團隊"
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                footer={null}>
                <div>
                    <div className='wardinfousermodelinfo'><t2>團隊人員</t2></div>
                    <div className='wardinfousermodelinfocontext'>
                        <div className='wardinfousercontextinfo'><t2>主治醫師 {Bdoc}</t2></div>
                        <div className='wardinfousercontextinfo'><t2>住院醫師 {Ddoc}</t2></div>
                        <div className='wardinfousercontextinfo'><t2>專科護理師 {Ndoc}</t2></div>
                    </div>
                </div>
            </Modal>
        );
    }
    render() {
        //search CASEID
        const { data } = this.props
        const userinfo = data
        // const { wardvalue } = this.props
        // const flag = undefined
        function statusspan(source) {
            if (source.item === 'Respirator') {
                return 'gridtwocoltwo'
            }
            else return 'gridtwocolone'
        }
        function style_change(source) {
            if (source.item === 'Respirator') {
                if (source.data === 'false') {
                    return 'flagfalse'
                }
                if (source.data === 'non-invasive') {
                    return 'flagtrue'
                }
                return 'flagtrue'
            }
            else {
                if (source.data) {
                    return 'flagtrue'
                }
                else {
                    return 'flagfalse'
                }
            }
        }
        function flaglist() {
            const flag = [
                //{ "item": "Depends", "time": 1575531000, "data": true }, // 這是呼吸？ 應該會有三態才對
                { "item": "Respirator", "time": 1575531000, "data": 'false' }, // 呼吸我先改這樣
                { "item": "TPN", "time": 1575531000, "data": true },
                { "item": "NPO", "time": 1575531000, "data": false },
                { "item": "Anti", "time": 1575531000, "data": false },
                { "item": "Foley", "time": 1575531000, "data": true },
                { "item": "Drain", "time": 1575531000, "data": false }
            ]
            var flaglist = []
            for (let index = 0; index < flag.length; index++) {
                const element = flag[index];
                flaglist.push(
                    <t4 className={style_change(element)}>{wardstatestringformat(element)}</t4>
                )

            }
            return flaglist
        }
        function weightvalue(Weightlist) {
            if (Weightlist === undefined) {
                return 0
            }
            if (Weightlist.length === 0) {
                return 0
            }
            console.log(Weightlist)
            return Weightlist[0]
        }
        function Docvalue(mainDoc) {
            if (mainDoc.length === 0) {
                return ""
            }
            return mainDoc.PDDOCNMC
        }
        const weight = weightvalue(this.state.WeightList)
        const DocList = this.state.PDOCNEW
        const mainDoc = Docvalue(DocList.filter(function (value, index, arr) {
            return value.PDTYPE === "B"
        }))
        const PBASINFO = this.state.PBASINFO
        console.log(PBASINFO)
        if (PBASINFO.length === 0) {
            return null
        }
        return (
            <div className='wardinfouserlayout'>
                {this.Doc_Display()}
                <div className='wardinfopatient'>
                    <div style={{ height: '50px', display: 'grid', gridTemplateColumns: "1fr 1fr 1fr" }}>
                        <div style={{ alignItems: "center", display: "flex" }}>
                            <t2 className='wardblacktoindex' onClick={() => this.sendData(null)}>&larr;返回總覽</t2>
                        </div>
                        <centerstyle>
                            <t1>病人資訊摘要</t1>
                        </centerstyle>
                    </div>
                    <div style={{ height: "50px" }}>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <t2>床號:{Number(data.HBEDNO)},{data.PNAMEC.toString().trim()},{moment(PBASINFO.PBIRTHDT, "YYYYMMDD").format("YYYY/MM/DD")},{data.PHISTNUM.toString()}</t2>
                            <div className='wardinfouserflagboard'>
                                {flaglist()}
                            </div>
                        </div>
                        <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                            <div style={{ display: "flex", alignItems: "center" }}>
                                <t2 style={{ marginRight: "16px" }}>[{this.state.Bornweek}+{this.state.Bornday}]&rarr;{pregancyweeksanddays(this.state.Bornweek, this.state.Bornday, moment(PBASINFO.PBIRTHDT, 'YYYYMMDD').format('YYYY/MM/DD'))}</t2>
                                <t4 style={{ marginRight: "16px" }}>{this.state.BornWeight}g&rarr;{weight}g</t4>
                                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", width: "70px" }}>
                                    <img src={fileLogo} alt='fileLogo'></img>
                                    <t4>{userinfo.report}</t4>
                                    <img src={file2Logo} alt='file2Logo'></img>
                                    <t4>{userinfo.nonreport}</t4>
                                </div>
                            </div>
                            <div style={{ display: "grid", gridTemplateColumns: "auto auto", gridColumnGap: "10px" }}>
                                <t3>主治醫師 {mainDoc}</t3>
                                <t3 className='wardblacktoindex' onClick={this.showModal}>查看醫療團隊資訊</t3>
                            </div>
                        </div>
                    </div>
                    <div className='wardinfousercontent'>
                        <div style={{ padding: "10px 15px" }}>
                            {/* <WardChart HISID={data.PHISTNUM}></WardChart> */}
                        </div>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px" }}>
                            <div style={{ borderRadius: "4px" }}>
                                <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                    <t2>檢驗項目</t2>
                                </div>
                                {/* <Labviewlayout PickerDate={moment(moment().format('YYYYMMDD'), 'YYYYMMDD').valueOf()} RESLAB01={this.state.RESLAB01} RESLAB02={this.state.RESLAB02} RESDBGAS={this.state.RESDBGAS} RESLAB0C={this.state.RESLAB0C} RESDGLU1={this.state.RESDGLU1} RESSECT={this.state.RESSECT}></Labviewlayout> */}
                            </div>
                            <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                                <Wardgrewchart userinfo={userinfo}></Wardgrewchart>
                            </div>
                        </div>
                        <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px", marginTop: "10px" }}>
                            <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                <t2>病人重要注意事項</t2>
                            </div>
                            <div style={{ padding: "0px 15px", maxHeight: "200px", minHeight: "50px" }}>
                                <div style={{ padding: "5px 0px" }}>
                                    <t3>1.對甲殼類食物過敏</t3>
                                </div>
                            </div>
                        </div>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridColumnGap: "10px", marginTop: "10px" }}>
                            <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                                <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                    <t2>排程</t2>
                                </div>
                                <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                    <div style={{ height: "50px", display: 'grid', gridTemplateColumns: "25% 75%", borderBottomWidth: "1px", borderBottomColor: "rgba(232, 232, 232, 1)", borderBottomStyle: "solid" }}>
                                        <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>6/16 16:00</div>
                                        <div style={{ fontSize: "1rem", display: "flex", paddingLeft: "15px", alignItems: "center" }}>個案家屬來訪</div>
                                    </div>
                                </div>
                            </div>
                            <div style={{ border: "1px solid rgba(232,232,232,1)", borderRadius: "4px" }}>
                                <div style={{ backgroundColor: "#cccccc", borderTopLeftRadius: "4px", borderTopRightRadius: "4px", height: "50px", padding: "0px 16px", display: "flex", alignItems: "center" }}>
                                    <t2>待辦事項</t2>
                                </div>
                                <div style={{ maxHeight: "200px", overflow: 'auto', minHeight: "50px" }}>
                                    <Wardinfousercheckbox selectstate={true}></Wardinfousercheckbox>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Wardinfouser;