import React, { Component } from 'react';
import { Labwarningvalue } from '../../Function/Wardstate'

class Labviewitem extends Component {
    render() {
        const { value, title, time } = this.props
        return (
            <div style={{ display: "grid", gridTemplateRows: "1fr 2fr 1fr", height: "120px" }}>
                <centerstyle>
                    <t3>{title}</t3>
                </centerstyle>
                <centerstyle>
                    <t2 style={{ fontSize: "24px", color: Labwarningvalue(value, title) ? "black" : "red" }}>{value}</t2>
                </centerstyle>
                <centerstyle>
                    <t3 style={{ color: "rgba(0,0,0,0.4)" }}>{time}</t3>
                </centerstyle>
            </div>
        );
    }
}

export default Labviewitem