export function Select_Main_Doc(DOCLIST) {
    console.log(DOCLIST)
    const MainDoc = DOCLIST.filter(function (value, index, array) {
        return value.PDTYPE === "B"
    })
    if (MainDoc.length === 0) {
        return ""
    }
    else {
        const data = MainDoc[0]
        return data.PDDOCNMC
    }
}

export function SumFeedDataMapReduce(arr) {
    const sumlist = arr.map(function (value, index) {
        return Number(value.inp_val2)
    })
    var sum = 0
    for (let index = 0; index < sumlist.length; index++) {
        const element = sumlist[index];
        sum = sum + element
    }
    return sum
}

