import React, { Component } from 'react';
import Vitalsign from './Vitalsign';
import Controlbar from '../Controlbar';
import Crossection_io from './Crossection_io'
import Labviewlayout from './Labview'
import Nonlabviewlayout from './Nonlabview'
import GAS from './GAS'
import RTbreathe from './RTbreathe'
import Prescriptionorderlayout from '../Prescriptionorder/Prescriptionorder'
import Treatmentorderlayout from '../Treatmentorder/Treatmentorder'
import Crossection_schedule from './Crosssectionschedule'
import Crosssection_todo from './Crosssection_todo'
import { yyyymmddHHmmfromat, yyyymmddformat, HISAPI_stringtodate } from '../../Function/Timeformat'
import './Crosssectionlayout.scss'
import * as ApiProvider from '../../API/ApiProvider';
import X2JS from 'x2js';
import moment from 'moment'

const SCHEDULE_TYPE_EVENT = "事件";
const timeZoneOffset = 8 * 60 * 60 * 1000;

class Crosssectionlayout extends Component {
    state = {
        Cross_item: [0, 1, 2, 3, 4, 5],
        PickerDate: moment(moment().format('YYYYMMDD'), 'YYYYMMDD').valueOf(),
        QDCLIST: [],
        RESDGLU1: [],
        RESLAB02: [],
        RESLAB01: [],
        RESDBGAS: [],
        RESLAB0C: [],
        ORDLIST: [],
        RESSECT: [],
        QSPE: [],
        UDORDER0: [],
        TRTMNTQ1: [],
        caseinfo: this.props.caseinfo,
        scheduleRawData: [],
        scheduleData: [],
        noticeRawData: [],
        startTimestamp: 0
    }
    componentDidMount() {
        this.NIS_API_IO(this.state.PickerDate)

        const { startDate, endDate } = this.getStartEndTime();
        this.querySchedule(startDate, endDate);
        this.queryNotice();
    }

    NIS_API_IO(Datetime) {
        function selectMonthInterval(Datetime) {
            const timestampInterval = new Date().getTime() - Datetime
            const timestampMonth = 30 * 24 * 60 * 60 * 1000
            const MonthInt = parseInt(timestampInterval / timestampMonth) + 1
            return MonthInt
        }
        const xm2 = new X2JS()
        const { caseinfo } = this.props
        const st = moment(Datetime + (7 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const et = moment(Datetime + (31 * 60 * 60 * 1000)).format("YYYYMMDDHHmm")
        const st_ISOstring = moment(st, 'YYYYMMDDHHmm').format('YYYY-MM-DD[T]HH:mm:ss')
        const et_ISOstring = moment(et, 'YYYYMMDDHHmm').format('YYYY-MM-DD[T]HH:mm:ss')
        const TRANSDate = moment(caseinfo.TRANSINDT, 'YYYYMMDD').format("YYYY-MM-DD")
        const MonthInterval = selectMonthInterval(Datetime)
        const QDCdata = ApiProvider.getQDC(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data))
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                return parsedata

            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESDGLU1 = ApiProvider.getStoredProcedureRESDGLU1(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB02 = ApiProvider.getStoredProcedureRESLAB02(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB01 = ApiProvider.getStoredProcedureRESLAB01(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESDBGAS = ApiProvider.getStoredProcedureRESDBGAS(caseinfo.HISID, TRANSDate)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const RESLAB0C = ApiProvider.getStoredProcedureRESLAB0C(caseinfo.HISID, TRANSDate)
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getQDC NG " + JSON.stringify(err));
            });
        const ORDLIST = ApiProvider.getStoredProcedureORDLIST(caseinfo.HISID, TRANSDate, "ALL", "DOC3924B")
            .then(data => {
                console.log(data.data)
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const RESSECT = ApiProvider.getStoredProcedureRESSECT(caseinfo.HISID, MonthInterval, "ALL", "DOC3924B")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const QSPE = ApiProvider.getQSPE(caseinfo.CASEID, st, et)
            .then(data => {
                console.log(xm2.xml2js(data.data).outmsg.resconts.rescont)
                const parsedata = xm2.xml2js(data.data).outmsg.resconts.rescont
                if (Array.isArray(parsedata)) {
                    return parsedata
                }
                else {
                    const arr = []
                    arr.push(parsedata)
                    console.log(arr)
                    return arr
                }
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const UDORDER0 = ApiProvider.getStoredProcedureUDORDER0(caseinfo.HISID, caseinfo.CASEID, "123")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });
        const TRTMNTQ1 = ApiProvider.getStoredProcedureTRTMNTQ1(caseinfo.HISID, caseinfo.CASEID)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureORDLIST NG " + JSON.stringify(err))
            });

        Promise.all([QDCdata, RESDGLU1, RESLAB02, RESLAB01, RESDBGAS, RESLAB0C, ORDLIST, RESSECT, QSPE, UDORDER0, TRTMNTQ1]).then(result => {
            // console.log("API DATA = " + result[8])
            this.setState({
                QDCLIST: result[0], //IO要用的
                RESDGLU1: result[1], //累積報告
                RESLAB02: result[2],  //累積報告
                RESLAB01: result[3],  //累積報告
                RESDBGAS: result[4],  //累積報告
                RESLAB0C: result[5],  //累積報告
                ORDLIST: result[6],
                RESSECT: result[7],
                QSPE: result[8],
                UDORDER0: result[9],
                TRTMNTQ1: result[10]
            })
        })
    }
    getStartEndTime() {
        const pdate = yyyymmddformat(this.state.PickerDate)
        const pickerDateYear = pdate.split('/')[0];
        const pickerDateMonth = pdate.split('/')[1];
        const pickerDateDate = pdate.split('/')[2];
        console.log(pickerDateYear, pickerDateMonth)
        const startDateBegin = new Date(pickerDateYear, pickerDateMonth, pickerDateDate).getTime() + timeZoneOffset;

        const startTimestamp = startDateBegin;
        const endTimestamp = startTimestamp + 1 * 24 * 60 * 60 * 1000;

        this.setState({
            startTimestamp: startTimestamp
        });

        return { startTimestamp, endTimestamp };
    }
    querySchedule(startTimestampStr, endTimestampStr) {
        ApiProvider.getOnePatientSchedule(this.state.caseinfo.TRANSID, startTimestampStr, endTimestampStr)
            .then(data => {
                console.log("getOnePatientSchedule OK " + JSON.stringify(data.data));

                const itemList = data.data;
                let refactoredList = [];
                itemList.forEach(item => {
                    const itemTimeStr = item.time.split('T')[0];
                    let foundItemIndex = refactoredList.findIndex(element => element.time === itemTimeStr);
                    if (foundItemIndex === -1) {
                        //not found, create one
                        let newItem = {
                            time: itemTimeStr,
                            timestamp: item.time,
                            scheduleList: []
                        }
                        let newEvent = {
                            type: item.type,
                            timestamp: new Date(item.time).getTime(),
                            writer: (item.type === SCHEDULE_TYPE_EVENT) ? "林醫師" : "張醫師",
                            name: (item.type === SCHEDULE_TYPE_EVENT) ? "林ＯＯ" : "黃ＯＯ",
                            content: item.context,
                            bednumber: item.bednumber
                        }
                        newItem.scheduleList.push(newEvent);
                        refactoredList.push(newItem);
                    } else {
                        //found, update item
                        let newEvent = {
                            type: item.type,
                            timestamp: new Date(item.time).getTime(),
                            writer: (item.type === SCHEDULE_TYPE_EVENT) ? "林醫師" : "張醫師",
                            name: (item.type === SCHEDULE_TYPE_EVENT) ? "林ＯＯ" : "黃ＯＯ",
                            content: item.context,
                            bednumber: item.bednumber
                        }
                        refactoredList[foundItemIndex].scheduleList.push(newEvent);
                    }
                });
                this.setState({
                    scheduleRawData: data.data,
                    scheduleData: refactoredList
                });
            })
            .catch(err => {
                console.log("getOnePatientSchedule NG " + JSON.stringify(err));
            });
    }
    queryNotice() {
        ApiProvider.getOnePatientNotice(this.state.caseinfo.TRANSID, this.props.caseinfo.CASEID)
            .then(result => {
                console.log("getOnePatientNotice OK " + JSON.stringify(result));
                this.setState({
                    noticeRawData: result.data
                })
            })
            .catch(err => {
                console.log("getOnePatientNotice NG " + JSON.stringify(err));
            });
    }
    getDataFromTimestamp(timestamp) {
        console.log(timestamp)
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const dataOfDayIndex = this.state.scheduleData.findIndex(element => element.time === dateStr);
        const dataOfDay = (dataOfDayIndex === -1) ? { scheduleList: [] } : this.state.scheduleData[dataOfDayIndex];
        return dataOfDay;
    }
    getNoticeFromTimestamp(timestamp) {
        const dateStr = new Date(timestamp).toISOString().split('T')[0];
        const noticeOfDayArray = this.state.noticeRawData.filter(element => element.time.indexOf(dateStr) > -1);
        return noticeOfDayArray;
    }

    callbackFunction = (item_array) => {
        this.setState(
            {
                Cross_item: item_array
            }
        )
    }

    callbackDateFunction = (Datetime) => {
        this.setState({ PickerDate: Datetime }, () => {
            const { startDate, endDate } = this.getStartEndTime();
            this.querySchedule(startDate, endDate);
        });
    }


    display_item(number) {
        const item = this.state.Cross_item
        var filter = item.some(function (item, index, array) {
            return item === number
        });
        console.log(filter)
        return filter
    }
    render() {
        const dataOfDay = this.getDataFromTimestamp(this.state.startTimestamp);
        const noticeOfDay = this.getNoticeFromTimestamp(this.state.startTimestamp);
        const crosssectioncontrollist = [
            {
                "name": "生命徵象/輸出入",
                "scrollpoint": "vitalsignIO"
            },
            {
                "name": "檢驗結果",
                "scrollpoint": "lab"
            },
            {
                "name": "呼吸評估",
                "scrollpoint": "rt"
            },
            {
                "name": "醫藥囑",
                "scrollpoint": "order"
            },
            {
                "name": "行事曆及註記",
                "scrollpoint": "schedule"
            }
        ]
        const { caseinfo } = this.props
        if (this.state.RESDGLU1.length === 0) {
            return null
        }
        return (
            <div>
                <Controlbar caseinfo={caseinfo} Datestring={this.state.PickerDate} parentDateCallback={this.callbackDateFunction} parentCallback={this.callbackFunction} btnlist={crosssectioncontrollist} item_array={this.state.Cross_item}></Controlbar>
                <div id="crossection" className='crossectioncontent'>
                    <div id={"vitalsignIO"} style={this.display_item(0) ? { display: "grid", gridTemplateColumns: "2fr 1fr", gridGap: "10px", marginBottom: "10px" } : { display: "none" }}>
                        <Vitalsign caseinfo={caseinfo} defaultdate={this.state.PickerDate}></Vitalsign>
                        <Crossection_io PickerDate={this.state.PickerDate} QDCLIST={this.state.QDCLIST} caseinfo={caseinfo} title="I/O"></Crossection_io>
                    </div>
                    <div id={"lab"} style={this.display_item(1) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <Labviewlayout PickerDate={this.state.PickerDate} caseinfo={caseinfo} RESLAB01={this.state.RESLAB01} RESLAB02={this.state.RESLAB02} RESDBGAS={this.state.RESDBGAS} RESLAB0C={this.state.RESLAB0C} RESDGLU1={this.state.RESDGLU1} RESSECT={this.state.RESSECT}></Labviewlayout>
                        <Nonlabviewlayout ORDLIST={this.state.ORDLIST} RESSECT={this.state.RESSECT} caseinfo={caseinfo} PickerDate={this.state.PickerDate}></Nonlabviewlayout>
                    </div>
                    <div id={"rt"} style={this.display_item(2) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <GAS caseinfo={caseinfo} PickerDate={this.state.PickerDate} RESDBGAS={this.state.RESDBGAS}></GAS>
                        <RTbreathe caseinfo={caseinfo} PickerDate={this.state.PickerDate} QSPE={this.state.QSPE}></RTbreathe>
                    </div>
                    <div id={"order"} style={this.display_item(3) ? { display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" } : { display: "none" }}>
                        <Prescriptionorderlayout UDORDER0={this.state.UDORDER0} caseinfo={caseinfo} searchbar={false} PickerDate={this.state.PickerDate}></Prescriptionorderlayout>
                        <Treatmentorderlayout TRTMNT01={this.state.TRTMNTQ1} caseinfo={caseinfo} searchbar={false} PickerDate={this.state.PickerDate}></Treatmentorderlayout>
                    </div>
                    <div id={"schedule"} style={this.display_item(4) ? null : { display: "none" }}>
                        <Crossection_schedule noticeData={noticeOfDay} scheduleData={dataOfDay}></Crossection_schedule>
                    </div>
                    <div style={this.display_item(5) ? null : { display: "none" }}>
                        <Crosssection_todo caseinfo={caseinfo} ></Crosssection_todo>
                    </div>
                </div>
            </div>
        );
    }
}

export default Crosssectionlayout;