import React, { Component } from 'react';
import { bednumberformat, gender, birthdayweeks } from '../Function/Wardbedfunction'
import './Patientviewinfo.scss'
import { datestringformat } from '../Function/Timeformat';
class Patientinfo extends Component {

    state = { visible: false };
    render() {
        const { USERDATA,BEDNO,REMARK,MAINDOC,TRANSINDT,CASEID } = this.props
        return (
            <div className='patientinfolayout'> 
                <div className='patientintitle'>病床:{bednumberformat(BEDNO)}/{USERDATA.PNAMEC}/{gender(USERDATA.PSEX)}/{birthdayweeks(datestringformat(USERDATA.PBIRTHDT))}週歲</div>
                <div className='context'>
                    <div className='title'>病歷號碼:</div>
                    <div className='item'>{USERDATA.PHISTNUM}</div>
                </div>
                <div className='context'>
                    <div className='title'>住院編號:</div>
                    <div className='item'>{CASEID}</div>
                </div>
                <div className='context'>
                    <div className='title'>主治醫師:</div>
                    <div className='item'>{MAINDOC}醫師</div>
                </div>
                <div className='context'>
                    <div className='title'>轉入日期:</div>
                    <div className='item'>{TRANSINDT}</div>
                </div>
                <div className='context'>
                    <div className='titlenote'>備註:</div>
                    <div className='item'>{REMARK}</div>
                </div>
            </div>
        );
    }
}

export default Patientinfo