import React, { Component } from 'react'
import './Chronic_ordertable.scss'
import moment from 'moment'


class Chronicordertable extends Component {
    // 0:一天 1:三天 2:一週 3:兩週
    switch_state() {
        const { chronic_state } = this.props
        switch (chronic_state) {
            case 0:
                return 24
            case 1:
                return 21
            case 2:
                return 21
            case 3:
                return 14
            default:
                break;
        }
    }
    switch_state_title() {
        return "140px"
    }
    celltable() {
        function changegridnumber(key) {
            switch (key) {
                case 0:
                    return 24
                case 1:
                    return 18
                case 2:
                    return 21
                case 3:
                    return 14
                default:
                    break;
            }
        }
        const { chronic_state } = this.props
        const number = changegridnumber(chronic_state)
        var arr = []
        for (let index = 0; index < number; index++) {
            arr.push(
                <div className='cell'></div>
            )

        }
        return arr
    }
    render() {
        function changegridstyle(key) {
            switch (key) {
                case 0:
                    return 'ordertableoneday'
                case 1:
                    return 'ordertablethirdday'
                case 2:
                    return 'ordertableoneweek'
                case 3:
                    return 'ordertabletwoweek'
                default:
                    return null
            }
        }
        function ListData(udorder0, interval, pickerdate) {
            function starttime(i, d) {
                if (i === 0) {
                    return new Date(d).getTime() + (7 * 60 * 60 * 1000)
                }
                if (i === 1) {
                    return new Date(d).getTime() + (31 * 60 * 60 * 1000) - (3 * 24 * 60 * 60 * 1000)
                }
                if (i === 2) {
                    return new Date(d).getTime() + (31 * 60 * 60 * 1000) - (7 * 24 * 60 * 60 * 1000)
                }
                if (i === 3) {
                    return new Date(d).getTime() + (31 * 60 * 60 * 1000) - (14 * 24 * 60 * 60 * 1000)
                }
                return null
            }
            function TimeInterval(st, interval) {
                var timearray = []
                if (interval === 0) {
                    for (let index = 0; index < 24; index++) {
                        const time = st + (index * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (interval === 1) {
                    for (let index = 0; index < 18; index++) {
                        const time = st + (index * 4 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (interval === 2) {
                    for (let index = 0; index < 21; index++) {
                        const time = st + (index * 8 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                if (interval === 3) {
                    for (let index = 0; index < 14; index++) {
                        const time = st + (index * 24 * 60 * 60 * 1000)
                        timearray.push(time)
                    }
                }
                return timearray
            }
            function TimeArrayInterval(interval) {
                if (interval === 0) {
                    return (1 * 60 * 60 * 1000)
                }
                if (interval === 1) {
                    return (4 * 60 * 60 * 1000)
                }
                if (interval === 2) {
                    return (8 * 60 * 60 * 1000)

                }
                if (interval === 3) {
                    return (24 * 60 * 60 * 1000)
                }
                return null
            }
            function startTimeFormat(st, sttimeString) {
                if (sttimeString < st) {
                    return st
                }
                return sttimeString
            }
            function endTimeFormat(et, ettimestring) {
                if (et < ettimestring) {
                    return et
                }
                return ettimestring
            }
            function shiftWeight(startTime, st, et) {
                const width = document.body.clientWidth
                const shiftTimeStampWidth = width * (startTime - st) / (et - st)
                console.log( (startTime - st))
                return shiftTimeStampWidth

            }
            function WeightWidth(startTime, EndTime, st, et) {
                const width = document.body.clientWidth
                const TimeStampWidth = width * (EndTime - startTime) / (et - st)
                console.log( (EndTime - startTime))
                console.log( (EndTime))
                return TimeStampWidth
            }

            const et = new Date(pickerdate).getTime() + (31 * 60 * 60 * 1000)
            const st = starttime(interval, pickerdate)
            const IntervalArray = TimeInterval(st, interval)
            var arraylist = []
            if (Array.isArray(udorder0)) {
                for (let index = 0; index < udorder0.length; index++) {
                    const element = udorder0[index];
                    const sttimeString = moment(element.UDBGNDT.toString(), "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
                    const ettimeString = moment(element.UDENDDT.toString(), "YYYY-MM-DD-hh.mm.ss.000000").valueOf()
                    if (et < sttimeString || st > ettimeString) {
                    }
                    else {
                        const StartTime = startTimeFormat(st, sttimeString)
                        const EndTime = endTimeFormat(et, ettimeString)
                        const shift = shiftWeight(StartTime, st, et)
                        const width = WeightWidth(StartTime, EndTime, st, et)
                        arraylist.push({
                            "width": width,
                            "shift": shift,
                            "element": element
                        })
                    }
                }
            }
            return arraylist
        }



        const { chronic_state, caseinfo, UDORDER0, PickerDate } = this.props
        console.log(UDORDER0)
        console.log(ListData(UDORDER0, chronic_state, PickerDate))
        const datalist = ListData(UDORDER0, chronic_state, PickerDate)

        const Tablecell = datalist.map(function (value, index, array) {
            function formatString(value) {
                if (value === undefined || value === null) {
                    return "--"
                }
                return value.toString().trim()
            }
            return (
                <div key={index} style={{ padding: "10px 0px", paddingLeft: value.shift + "px" }}>
                    <div style={{ width: value.width + "px", height: "20", backgroundColor: "#3B97E1", color: "white", padding: "0px 5px" }}>{formatString(value.element.UDGNAME)}/{formatString(value.element.UDDRGNAM)}</div>
                </div>
            );
        })
        return (
            <div style={{ display: "grid", gridTemplateColumns: "140px 1fr", position: 'relative' }}>
                <div style={{ background: "#CCCCCC", display: "flex", justifyContent: "center", alignItems: "center" }}>
                    <t2>醫藥囑</t2>
                </div>
                <div className={changegridstyle(chronic_state)}>{this.celltable()}</div>
                <div style={{ position: 'absolute', left: 140, height: "250px", overflow: "auto" }}>
                    {Tablecell}
                </div>
            </div>
        );
    }
}

export default Chronicordertable