import React, { Component } from 'react';
import chooseedit from '../../Image/svg/Chooseedit.svg'
import './RTbreathtablecell.scss'
import * as FiliterReg from '../../Function/FiliterReg'
import RemarkModal from '../CrossSectionView/RemarkModal'

class RTbreathetablecell extends Component {
    state = {
        onCell: false,
        onEdit: false,
        remark: this.props.value.remark
    }
    componentDidMount() {

    }
    Mouse_move() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: true
                }
            )
        }
    }

    Mouse_leave() {
        if (this.state.onEdit) {

        }
        else {
            this.setState(
                {
                    onCell: false
                }
            )
        }
    }

    Edit_state() {
        this.setState(
            {
                onEdit: true
            }
        )
    }
    handleOk = newRemarkValue => {
        console.log(newRemarkValue);
        this.setState({
            onEdit: false,
            remark: newRemarkValue
        });
    };

    handleCancel = e => {
        console.log(e);
        this.setState({
            onEdit: false,
        });
    };
    render() {
        const { value } = this.props
        console.log(value)
        return (
            <div>
                <RemarkModal caseinfo={this.props.caseinfo} type="breath" createTime={value.Date} name={value.Mode} visible={this.state.onEdit} remark={this.state.remark} onCancel={this.handleCancel} onOk={this.handleOk}></RemarkModal>
                <div onMouseMove={() => this.Mouse_move()} onMouseLeave={() => this.Mouse_leave()} className="RTtablecell">
                    <t3 className='RTBreathDateCell'>{value.Date}{value.Time}</t3>
                    <div style={{ display: "grid", gridTemplateRows: "50px 50px" }}>
                        <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr" }}>
                            <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{value.Mode}</t3>
                            <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{FiliterReg.QSPESetting(value)}</t3>
                        </div>
                        <t3 style={{ display: "flex", alignItems: "center", border: "rgba(238,238,238,1) 1px solid", borderCollapse: "collapse", padding: "0px 5px" }}>{this.state.remark}</t3>
                    </div>
                    <div style={this.state.onCell ? { display: "flex", alignItems: "center", justifyContent: "center", position: "absolute", right: 0, height: "130px", background: "white", width: "77px", border: "rgba(238,238,238,1) 1px solid" } : { display: "none" }}>
                        <div onClick={() => this.Edit_state()} style={{ width: "40px", height: "40px", borderRadius: "99em", background: "rgba(124, 179, 5, 1)", display: "flex", justifyContent: "center", alignItems: "center" }}>
                            <img src={chooseedit} alt='chooseedit'></img>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default RTbreathetablecell