import React, { Component } from 'react';
import Prescriptionordertable from '../Prescriptionorder/Prescriptionorder'
import Treatmentorderlayout from '../Treatmentorder/Treatmentorder'
import * as ApiProvider from '../../API/ApiProvider';

class Orderview extends Component {
    state = {
        UDORDER0: [],
        TRTMNTQ1: []
    }
    componentDidMount() {
        this.apidata()
    }
    apidata() {
        const { caseinfo } = this.props
        const UDORDER0 = ApiProvider.getStoredProcedureUDORDER0(caseinfo.HISID, caseinfo.CASEID, "123")
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureUDORDER0 NG " + JSON.stringify(err))
            });
        const TRTMNTQ1 = ApiProvider.getStoredProcedureTRTMNTQ1(caseinfo.HISID, caseinfo.CASEID)
            .then(data => {
                return data.data
            })
            .catch(err => {
                console.log("Andy test getStoredProcedureTRTMNTQ1 NG " + JSON.stringify(err))
            });
        Promise.all([UDORDER0, TRTMNTQ1]).then(result => {
            console.log(result)
            this.setState({
                UDORDER0: result[0],
                TRTMNTQ1: result[1]
            })
        })
    }
    render() {
        const { caseinfo } = this.props
        const udata = this.state.UDORDER0
        const tdata = this.state.TRTMNTQ1
        if (udata.length === 0 || tdata.length === 0) {
            //Loading
            return null
        }
        return (
            <div>
                <div style={{ maxHeight: '75vh', overflowY: 'auto' }}>
                    <div style={{ display: "grid", gridTemplateColumns: "1fr 1fr", gridGap: "10px" }}>
                        <Prescriptionordertable UDORDER0={this.state.UDORDER0} searchbar={true} caseinfo={caseinfo}></Prescriptionordertable>
                        <Treatmentorderlayout TRTMNT01={this.state.TRTMNTQ1} searchbar={true} caseinfo={caseinfo}></Treatmentorderlayout>
                    </div>
                </div>
            </div>
        );
    }
}

export default Orderview;